import React from "react";
import styled from "styled-components";
import { BoxContainer, HorizontalLines, SubHeader } from "../../../style/Gobalstyle";
import { MenusectionImage1 } from "../../../Utils/Images";


export default function ViewModifierGroup() {
  return (
    <div>
      <SubHeader>
        <p>View Modifier </p>
      </SubHeader>

      <BoxContainer>
        <InnerUpperViewModifier>
          <header>Modifiers</header>
          <HorizontalLines />
          <section>
            <div>
              <span>Name</span>
              <aside>Extras</aside>
            </div>

            <div>
              <span>Modifiers</span>
              <GridView3>
                <input placeholder="Extras" disabled />Extras
                <input placeholder="Extras" disabled />Extras
                <input placeholder="Extras" disabled />Extras
              </GridView3>
            </div>
          </section>
        </InnerUpperViewModifier>
      </BoxContainer>

      <BoxContainer>
        <InnerLowerViewModifier>
          <header>Linked Items</header>

          <LinkedItems>
            <img src={MenusectionImage1} />

            <div>
              <h1>Burger</h1>
              <h4>Veg n Crisp Burger</h4>
            </div>
          </LinkedItems>

          <LinkedItems>
            <img src={MenusectionImage1} />

            <div>
              <h1>Burger</h1>
              <h4>Veg n Crisp Burger</h4>
            </div>
          </LinkedItems>

          <LinkedItems>
            <img src={MenusectionImage1} />

            <div>
              <h1>Burger</h1>
              <h4>Veg n Crisp Burger</h4>
            </div>
          </LinkedItems>

          <LinkedItems>
            <img src={MenusectionImage1} />

            <div>
              <h1>Burger</h1>
              <h4>Veg n Crisp Burger</h4>
            </div>
          </LinkedItems>
        </InnerLowerViewModifier>
      </BoxContainer>
    </div>
  );
}

const GridView3 = styled.div`

display: grid;
grid-template-column: repeat(3 , 1fr)
gap:10px;

span {

    width: 100%;
    background: #fcfdfe;
    border: 1px solid #f0f1f7;
    border-radius: 8px;
    padding: 12px 20px;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
  
}

`;



const InnerUpperViewModifier = styled.div`

  header {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.05em;
    color: #000000;
  }

  span {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    color: #000000;
    opacity: 0.6;
  }

  section {
    width: 566px;
  }


`;

const InnerLowerViewModifier = styled.div`
  header {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.05em;
    color: #000000;
  }
`;

const LinkedItems = styled.div`
  width: 100%;
  margin: 8px 0;
  background: #fcfdfe;
  border: 1px solid #f0f1f7;
  border-radius: 8px;
  padding: 18px 20px;
  display: flex;

  img {
    width: 75px;
    height: 67px;
    border-radius: 8px;
    object-fit: cover;
  }

  h1 {
    font-family: "Jost";
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    line-height: 27px;
    color: #202020;
  }

  div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 20px;
  }

  h4 {
    font-family: "Jost";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #838383;
  }
`;
