import React from "react";
import styled from "styled-components";
import ToggleSwitch from "../../../components/ToggleSwitch";
import {
  BlackButton,
  BoxContainer,
  OrangeButton,
  SubHeader,
  WhiteButton,
} from "../../../style/Gobalstyle";
import {
  InnerLeftWrapper,
  InnerWrapper,
  InputPageLeftWrapper,
  InputPageRightWrapper,
  InputPageWrapper,
  InputPriceBoxWrapper,
  InputWrap,
  MiddleContent,
  OptionSetWrap,
  PriceBox,
  PriceContainer,
  PriceInnerBox,
  UpdateImageSection,
  Upload,
} from "../MenuStyle";

export default function CreateSectionItem() {
  return (
    <div>
      <SubHeader>
        <p>Create Item</p>
      </SubHeader>

      <BoxContainer>
        <UpdateImageSection>
          <Upload>
            <input type="file" />
            <i className="icon-Upload" />
          </Upload>
          <div>
            <WhiteButton>CHANGE PHOTO</WhiteButton>
            <OrangeButton>REMOVE PHOTO</OrangeButton>
          </div>
        </UpdateImageSection>

        <InputPageWrapper>
          <InputWrap>
            <section>
              <label>Name</label>
            </section>
            <input dir="ltr" type="text" placeholder="Name" />
            <input dir="rtl" type="text" placeholder="dc" />
          </InputWrap>

          <InputWrap>
            <section>
              <label>Sections</label>
            </section>
            <input type="text" placeholder="Cake" />
          </InputWrap>
        </InputPageWrapper>
        <InnerWrapper>
          <InputPageLeftWrapper>
            <InnerLeftWrapper>
              <InputWrap>
                <section>
                  <label>Menus</label>
                </section>
                <input type="text" placeholder="DOWNDROP" />
              </InputWrap>

              <InputWrap>
                <section>
                  <label>Sort Order ID</label>
                </section>
                <input type="text" placeholder="14120" />
              </InputWrap>

              <InputWrap>
                <section>
                  <label>Description / وصف </label>
                </section>
                <input dir="ltr" type="text" placeholder="Description" />
                <input dir="rtl" type="text" placeholder="وصف" />
              </InputWrap>

              <div>
                <InputWrap>
                  <section>
                    <label>Sort Order ID</label>
                  </section>
                </InputWrap>
                <ToggleSwitch label="saxsa" />
              </div>

              <InputWrap>
                <section>
                  <label>Recommended Items</label>
                </section>
                <input dir="ltr" type="text" placeholder="Recommended Items" />
              </InputWrap>
            </InnerLeftWrapper>
          </InputPageLeftWrapper>
          <InputPageRightWrapper>
            <PriceContainer>
              <InputWrap>
                <section>
                  <label>Price / سعر</label>
                </section>
              </InputWrap>
              <PriceBox>
                <PriceInnerBox>
                  <InputPriceBoxWrapper>
                    <InputWrap>
                      <section>
                        <label>Name</label>
                      </section>
                      <input type="text" placeholder="Name" />
                    </InputWrap>

                    <InputWrap>
                      <section>
                        <label>Price</label>
                      </section>
                      <input type="text" placeholder="Name" />
                    </InputWrap>

                    <InputWrap>
                      <section>
                        <label>Calories</label>
                      </section>
                      <input type="text" placeholder="Calories" />
                    </InputWrap>

                    <InputWrap>
                      <section>
                        <label>اسم</label>
                      </section>
                      <input dir="rtl" type="text" placeholder="اسم" />
                    </InputWrap>

                    <InputWrap>
                      <section>
                        <label>سعر</label>
                      </section>
                      <input dir="rtl" type="text" placeholder="سعر" />
                    </InputWrap>

                    <InputWrap>
                      <section>
                        <label>سعرات حراريه</label>
                      </section>
                      <input dir="rtl" type="text" placeholder="سعرات حراريه" />
                    </InputWrap>
                  </InputPriceBoxWrapper>
                </PriceInnerBox>

                <PriceInnerBox>
                  <InputPriceBoxWrapper>
                    <InputWrap>
                      <section>
                        <label>Name</label>
                      </section>
                      <input type="text" placeholder="Name" />
                    </InputWrap>

                    <InputWrap>
                      <section>
                        <label>Price</label>
                      </section>
                      <input type="text" placeholder="Name" />
                    </InputWrap>

                    <InputWrap>
                      <section>
                        <label>Calories</label>
                      </section>
                      <input type="text" placeholder="Calories" />
                    </InputWrap>

                    <InputWrap>
                      <section>
                        <label>اسم</label>
                      </section>
                      <input dir="rtl" type="text" placeholder="اسم" />
                    </InputWrap>

                    <InputWrap>
                      <section>
                        <label>سعر</label>
                      </section>
                      <input dir="rtl" type="text" placeholder="سعر" />
                    </InputWrap>

                    <InputWrap>
                      <section>
                        <label>سعرات حراريه</label>
                      </section>
                      <input dir="rtl" type="text" placeholder="سعرات حراريه" />
                    </InputWrap>
                  </InputPriceBoxWrapper>
                </PriceInnerBox>
                <span>+Add More</span>
              </PriceBox>
            </PriceContainer>

            <InputWrap style={{ marginBottom: "10px" }}>
              <section>
                <label>Allergies</label>
              </section>
              <input dir="ltr" type="text" placeholder="Allergies" />
            </InputWrap>

            <div>
              <InputWrap style={{ marginBottom: "10px" }}>
                <section>
                  <label>Mark Section as Signature</label>
                </section>
              </InputWrap>
              <ToggleSwitch label="Signature" />
            </div>
          </InputPageRightWrapper>
        </InnerWrapper>
        <OptionSetWrap>
          <header>Option Sets</header>
          <section>
            <div>
              <p>Modifier Group</p>
              <select>
                <option selected="true" disabled hidden></option>
                <option>Extras chesses</option>
                <option>Extras topping</option>
              </select>
            </div>

            <div>
              <p>Max</p>
              <input type="number" />
            </div>

            <div>
              <p>Min</p>
              <input type="number" />
            </div>

            <div>
              <p>Required</p>
              <select>
                <option selected="true" disabled hidden></option>
                <option>Yes</option>
                <option>No</option>
              </select>
            </div>

            <div>
              <p>Action</p>
              <div className="iconWrapper">
                <i className="icon-Delete" />
              </div>
            </div>
          </section>
        </OptionSetWrap>
        <MiddleContent style={{ marginTop: "20px" }}>
          <BlackButton>CREATE</BlackButton>
        </MiddleContent>
      </BoxContainer>
    </div>
  );
}
