import React,{useState} from "react";
import { BoxContainer, OrangeButton, SubHeader } from "../../../style/Gobalstyle";
import MenuTable from "../MenuTable";
import OptionModuleCreate from "../OptionModule/OptionModuleCreate";

export default function OptionModule() {
  const [openCreateOptionModule, setOpenCreateOptionModule] = useState(false);

  const OpenCreateOptionModule = () => {
    setOpenCreateOptionModule(true);
  };

  const UserHeader = [
    
    {
      id: "Name",
      key: "Name",
    },
    {
      id: "Modifiers",
      key: "Modifiers",
    },
    {
      id: "Action",
      key: "Action",
    },
    {
      id: "",
      key: "Toggle",
    },
  ];

  const TableAction = {
    apply: true,
    view: true,
    edit: true,
    delete: true,
    toggle: true,
    actionOfView: 'CreateModifierGroup'

  };

  const tableContents = [
    {
      Name: "Extras",
      Modifiers: "3",
      active: true,
    },
    {
      Name: "Add On",
      Modifiers: "3",
      active: true,
    },
  ];

  return (
    <div>

   {openCreateOptionModule && (
        <OptionModuleCreate
          open={openCreateOptionModule}
          handleClose={() => setOpenCreateOptionModule(false)}
        />
      )}
      <SubHeader>
        <p>Option Module</p>
        <OrangeButton onClick={OpenCreateOptionModule}>CREATE</OrangeButton>
      </SubHeader>

      <BoxContainer>
        <MenuTable
          header={UserHeader}
          tableData={tableContents}
          action={TableAction}
        />
      </BoxContainer>
    </div>
  );
}
