import React, { useEffect, useState } from "react";
import Dialog from "@mui/material/Dialog";
import useMediaQuery from "@mui/material/useMediaQuery";
import { useTheme } from "@mui/material/styles";
import { Cancel, OrangeButton } from "../../style/Gobalstyle";
import styled from "styled-components";
import { useSelector } from "react-redux";

export default function CreateQR({ open, handleClose }) {
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down("sm"));
  const direction = useSelector((state) => state.languageDirection.direction);

  return (
    <div>
      <Dialog
        fullScreen={fullScreen}
        open={open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <Cancel onClick={handleClose}>
          <i className="icon-CloseCross" />
        </Cancel>

        <CreateQRWrapper>
          <header>Add User</header>

          <InputWrap dir={direction}>
            <section>
              <label>First Name</label>
            </section>
            <input
              dir="ltr"
              type="text"
              placeholder=""
             
            />
          </InputWrap>

          <InputWrap dir={direction}>
            <section>
              <label>Last Name</label>
            </section>
            <input
              dir="ltr"
              type="text"
              placeholder=""
             
            />
          </InputWrap>

          <InputWrap dir={direction}>
            <section>
              <label>Email</label>
            </section>
            <input
              dir="ltr"
              type="email"
              placeholder=""
            
            />
          </InputWrap>

          <InputWrap dir={direction}>
            <section>
              <label>Role</label>
            </section>
            <select>
              <option selected="true" disabled hidden></option>
              <option>Owner</option>
              <option>Super Admin</option>
              <option>Admin</option>
              <option>Staff</option>
            </select>
          </InputWrap>
        
          <div style={{ textAlign: "center" }}>
            <OrangeButton>SUBMIT</OrangeButton>
          </div>
        </CreateQRWrapper>
      </Dialog>
    </div>
  );
}


const CreateQRWrapper = styled.div`
  width: 380px;
  background: #ffffff;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
  padding: 30px 20px;

  header {
    text-align: center;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    color: #000000;
  }
`;


const InputWrap = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-top: 10px;

  section {
    display: flex;
  }

  label {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    color: #000000;
    opacity: 0.6;
  }

  select {
    width: 100%;
    height: 42px;
    background: #fcfdfe;
    border: 1px solid #f0f1f7;
    border-radius: 8px;
    padding: 0 10px;
    margin: 5px 0;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
  }
  select:focus {
    outline: none;
    border: 1px solid #e1e1e1;
    box-shadow: 0px 2px 8px #f55a2c;
  }

  input {
    width: 100%;
    height: 42px;
    background: #fcfdfe;
    border: 1px solid #f0f1f7;
    border-radius: 8px;
    padding: 0 10px;
    margin: 5px 0;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
  }
  input:focus {
    outline: none;
    border: 1px solid #e1e1e1;
    box-shadow: 0px 2px 8px #f55a2c;
  }


`;



