import React from "react";
import styled from "styled-components";
import { BoxContainer, SubHeader } from "../../style/Gobalstyle";
import { CardWrapper } from "./FeedbackStyle";
import FeedbackTable from "./FeedbackTable";

export default function FeedbackSummary() {
  const UserHeader = [
    {
      id: "Name",
      key: "Name",
    },
    {
      id: "Phone Number",
      key: "phoneNumber",
    },
    {
      id: "Item Summary",
      key: "itemSummary",
    },
    {
      id: "QTY",
      key: "QTY",
    },
    {
      id: "Price",
      key: "Price",
    },
    {
      id: "Subtotal",
      key: "Subtotal",
    },
  ];

  const TableAction = {
    apply: false,
  };

  const tableContents = [
    {
      Name: "Dr. Joy",
      phoneNumber: "+4869412662",
      itemSummary: "Soy & Sesame Chicken Wings",
      QTY: "1",
      Price: "QAR 349",
      veg: "false",
      Subtotal: "QAR 349",
    },

    {
      Name: "Dr. hello",
      phoneNumber: "+4869412662",
      itemSummary: "Soy & Sesame Chicken Wings",
      QTY: "1",
      Price: "QAR 349",
      veg: "true",
      Subtotal: "QAR 349",
    },

    {
      Name: "Dr. Joy",
      phoneNumber: "+4869412662",
      itemSummary: "Soy & Sesame Chicken Wings",
      QTY: "1",
      Price: "QAR 349",
      veg: "false",
      Subtotal: "QAR 349",
    },
  ];

  return (
    <div>
      <SubHeader>
        <p>Order Number #256894</p>
      </SubHeader>
      <BoxContainer>
        <FeedbackTable
          header={UserHeader}
          tableData={tableContents}
          action={TableAction}
        />
      </BoxContainer>
      <CardWrapper>
        <OrderSummaryBox>
          <header>Order Summary</header>
          <div />
          <section>
            <label>Order Created: </label>
            <span>Sun, 7 May 2022</span>
          </section>
          <section>
            <label>Order Created: </label>
            <span>Sun, 7 May 2022</span>
          </section>
          <section>
            <label>Order Created: </label>
            <span>Sun, 7 May 2022</span>
          </section>
        </OrderSummaryBox>
      </CardWrapper>
    </div>
  );
}

const OrderSummaryBox = styled.div`
  width: 100%;
  background: #ffffff;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.28);
  border-radius: 10px;
  padding: 20px 25px;

  header {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.05em;
    color: #000000;
  }

  div {
    width: 100%;
    height: 1px;
    background: rgba(0, 0, 0, 0.3);
    margin: 5px 0;
  }

  section {
    margin: 7px 0;
  }
  label {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 17px;
    letter-spacing: 0.08em;
    color: #000000;
  }
  span {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 17px;
    letter-spacing: 0.08em;
    color: rgba(0, 0, 0, 0.6);
  }
`;
