import React from "react";
import { useNavigate } from "react-router-dom";
import {
  BoxContainer,
  FilterButton,
  FilterSection,
  OrangeButton,
  SubHeader,
} from "../../../style/Gobalstyle";
import MenuTable from "../MenuTable";

export default function SectionItem() {
  const navigate = useNavigate();
  const UserHeader = [
    {
      id: "Sort Order ID",
      key: "Sort_OrderID",
    },
    {
      id: "Name",
      key: "Name",
    },
    {
      id: "Menus",
      key: "Menus",
    },
    {
      id: "Sections",
      key: "Sections",
    },
    {
      id: "Price",
      key: "Price",
    },
    {
      id: "Action",
      key: "Action",
    },
    {
      id: "",
      key: "Toggle",
    },
  ];

  const TableAction = {
    apply: true,
    view: true,
    edit: true,
    delete: true,
    toggle: true,
    actionOfView: 'CreateSectionItem'
   
  };

  const tableContents = [
    {
      Sort_OrderID: "1234",
      Name: "French Fries",
      Menus: "Desserts",
      Sections: "Cake",
      Price: "SINGLE",
      active: true,
    },
    {
      Sort_OrderID: "5562",
      Name: "French Fries",
      Menus: "Desserts",
      Sections: "Cake",
      Price: "SINGLE",
      active: true,
    },
    {
      Sort_OrderID: "8452",
      Name: "French Fries",
      Menus: "Desserts",
      Sections: "Cake",
      Price: "SINGLE",
      active: true,
    },
    {
      Sort_OrderID: "9631",
      Name: "French Fries",
      Menus: "Desserts",
      Sections: "Cake",
      Price: "SINGLE",
      active: true,
    },
  ];

  return (
    <div>
      <SubHeader>
        <p>Section Items</p>
        <OrangeButton
          onClick={() => navigate(`/aglut/menu/sectionItems/create`)}
        >
          CREATE
        </OrangeButton>
      </SubHeader>

      <BoxContainer>
        <FilterSection>
          <FilterButton>
            <i className="icon-Filter" />
            <p>Filters</p>
          </FilterButton>

          <FilterButton>
            <i className="icon-Export" />
            <p>Export</p>
          </FilterButton>
        </FilterSection>

        <MenuTable
          header={UserHeader}
          tableData={tableContents}
          action={TableAction}
        />
      </BoxContainer>
    </div>
  );
}
