import React, { useState } from "react";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import ToggleSwitch from "../../components/ToggleSwitch";
import { ActionDiv, Table, TableWrapper, TBody, THead } from "../Menu/MenuStyle";
import ViewSectionItem from "../Menu/SectionItem/ViewSectionItem";

export default function UserManagementTable({ header, tableData, action }) {
  const direction = useSelector((state) => state.languageDirection.direction);

  return (
    <TableWrapper>
      <Table>
        <thead>
          <TableHeader data={header} dir={direction} />
        </thead>
        <div className="forMargin" />
        {tableData.map((item, index) => (
          <TableBody
            header={header}
            body={item}
            action={action}
            index={index}
            id={item.id}
            dir={direction}
          />
        ))}
      </Table>
    </TableWrapper>
  );
}

const TableHeader = ({ data, dir }) => {
  return (
    <THead dir={dir}>
      {data.map((item, index) => (
        <th key={index}>{item.id}</th>
      ))}
    </THead>
  );
};

const TableBody = ({ body, header, action, index, id, dir }) => {
  //   const [show, setShow] = useState();
  //   const navigate = useNavigate();
  //   const [openDelete, setOpenDelete] = useState();
  //   const [deleteData, setDeleteData] = useState({});
  //   const handleClickAway = () => {
  //     setShow(false);
  //   };

  return (
    <tbody className="tbody">
      <TBody dir={dir}>
        {header.map((el) => (
          <td style={{ padding: "0 5px" }}>
            {el.id === "Action" && action.apply ? (
              <TableAction action={action} data={body} />
            ) : el.key === "Toggle" && action.apply && action.toggle ? (
              <TableToggle data={body} />
            ) : (
              body[el.key]
            )}
          </td>
        ))}
      </TBody>
      <div className="forMargin" />
    </tbody>
  );
};

const TableToggle = ({ data }) => {
  return <>{data.active && <ToggleSwitch label={data.Sort_OrderID} />}</>;
};

const TableAction = ({ action, data }) => {
  const navigate = useNavigate();
  const [openViewSectionItem, setOpenViewSectionItem] = useState(false);

  const OpenViewSection = () => {
    if (action.actionOfView === "CreateSectionItem") {
      setOpenViewSectionItem(true);
    }
    if (action.actionOfView === "CreateModifierGroup") {
      navigate("/aglut/menu/optionModule/view/:123");
    }
  };

  return (
    <ActionDiv>
      {openViewSectionItem && (
        <ViewSectionItem
          open={openViewSectionItem}
          handleClose={() => setOpenViewSectionItem(false)}
        />
      )}

      {action.view && <i className="icon-View" onClick={OpenViewSection} />}
      {action.edit && (
        <i
          className="icon-Edit"
          onClick={() =>
            navigate(`/aglut/menu/sectionItems/edit/${data.Sort_OrderID}`)
          }
        />
      )}
      {action.delete && <i className="icon-Delete" />}
    </ActionDiv>
  );
};
