import React from "react";
import styled from "styled-components";
import { BoxContainer, SubHeader } from "../../../style/Gobalstyle";
import FeedbackTable from "../../Feedback/FeedbackTable";
import { CardWrapper } from "../OrderStyle";
import OrderTable from "../OrderTable";

export default function CompletedShowDetails() {
  const UserHeader = [
    {
      id: "Name",
      key: "Name",
    },
    {
      id: "Phone Number",
      key: "phoneNumber",
    },
    {
      id: "Item Summary",
      key: "itemSummary",
    },
    {
      id: "QTY",
      key: "QTY",
    },
    {
      id: "Price",
      key: "Price",
    },
    {
      id: "Subtotal",
      key: "Subtotal",
    },
  ];

  const TableAction = {
    apply: false,
  };

  const tableContents = [
    {
      Name: "Dr. Joy",
      phoneNumber: "+4869412662",
      itemSummary: "Soy & Sesame Chicken Wings",
      QTY: "1",
      Price: "QAR 349",
      veg: "false",
      Subtotal: "QAR 349",
    },

    {
      Name: "Dr. hello",
      phoneNumber: "+4869412662",
      itemSummary: "Soy & Sesame Chicken Wings",
      QTY: "1",
      Price: "QAR 349",
      veg: "true",
      Subtotal: "QAR 349",
    },

    {
      Name: "Dr. Joy",
      phoneNumber: "+4869412662",
      itemSummary: "Soy & Sesame Chicken Wings",
      QTY: "1",
      Price: "QAR 349",
      veg: "false",
      Subtotal: "QAR 349",
    },
  ];
  return (
    <div>
      <SubHeader>
        <p>Order Number #256894</p>
      </SubHeader>
      <BoxContainer>
        <FeedbackTable
          header={UserHeader}
          tableData={tableContents}
          action={TableAction}
        />
      </BoxContainer>
      <BoxContainer>
        <CompletedDetailWrapper>
          <LeftSection>
            <div>
              <label>Order No:</label>
              <span>01475</span>
            </div>

            <div>
              <label>Status:</label>
              <span>Order Completed</span>
            </div>

            <div>
              <label>Dine In:</label>
              <span>Table 1</span>
            </div>

            <div>
              <label>Takeaway:</label>
              <span>Cashier 1</span>
            </div>
          </LeftSection>
          <RightSection>
            <div>
              <label>Subtotal:</label>
              <span>QAR 345</span>
            </div>

            <div>
              <label>VAT:</label>
              <span>QAR 22.25</span>
            </div>

            <div>
              <label>Service Charge:</label>
              <span>QAR 22.25</span>
            </div>

            <div>
              <label style={{ fontSize: "20px" }}>Total:</label>
              <span style={{ fontSize: "20px" }}>QAR 577.25</span>
            </div>

            <div>
              <label>Mode of Payment:</label>
              <span>Credit Card</span>
            </div>

            <PrintReceipts>Print Receipts</PrintReceipts>
          </RightSection>
        </CompletedDetailWrapper>
      </BoxContainer>
    </div>
  );
}

const PrintReceipts = styled.div`
  width: 144px;
  height: 37px;
  background: #e1e1e1;
  border: 1px solid #b9b9b9;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;

  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  /* identical to box height */

  color: #515151;
`;

const CompletedDetailWrapper = styled.div`
  display: flex;
  justify-content: space-between;
`;

const LeftSection = styled.div`
  div {
    margin: 10px 0;
  }

  label {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.08em;
    color: #000000;
  }

  span {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.08em;
    margin: 0 10px;
    color: #646464;
  }
`;

const RightSection = styled.div`
  div {
    margin: 10px 0;
  }

  label {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.08em;
    color: #000000;
  }

  span {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.08em;
    margin: 0 10px;
    color: #646464;
  }
`;
