import React from "react";
import Dialog from "@mui/material/Dialog";
import useMediaQuery from "@mui/material/useMediaQuery";
import { useTheme } from "@mui/material/styles";
import {
  BoxContent,
  Cancel,
  CreateWrapper,
  InputPageWrapper,
  InputWrap,
  MiddleContent,
  Upload,
} from "../MenuStyle";
import { OrangeButton } from "../../../style/Gobalstyle";

export default function CreateSection({ open, handleClose }) {     
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down("md"));
  return (
    <div>
      <Dialog
        fullScreen={fullScreen}
        open={open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <Cancel onClick={handleClose}>
          <i className="icon-CloseCross" />
        </Cancel>
        <CreateWrapper>
          <BoxContent>
            <MiddleContent>
              <header>Create Section</header>
              <Upload>
                <input type="file" />
                <i className="icon-Upload" />
              </Upload>
            </MiddleContent>

            <InputPageWrapper>
              <InputWrap dir="ltr">
                <section>
                  <label>Name</label>
                </section>
                <input type="text" placeholder="Name" />
              </InputWrap>

              <InputWrap dir="rtl">
                <section>
                  <label> اسم</label>
                </section>
                <input type="text" placeholder=" اسم" />
              </InputWrap>



              <InputWrap dir="ltr">
                <section>
                  <label>Menus</label>
                </section>
                <input type="text" placeholder="Name" />
              </InputWrap>

              <InputWrap dir="rtl">
                <section>
                  <label> القوائم</label>
                </section>
                <input type="text" placeholder=" اسم" />
              </InputWrap>



              <InputWrap dir="ltr">
                <section>
                  <label>Sort Order ID</label>
                </section>
                <input type="text" placeholder="Name" />
              </InputWrap>

              <InputWrap dir="rtl">
                <section>
                  <label> معرّف ترتيب الفرز</label>
                </section>
                <input type="text" placeholder=" اسم" />
              </InputWrap>

              <InputWrap dir="ltr">
                <section>
                  <label>Description</label>
                </section>
                <input type="text" placeholder="Description" />
              </InputWrap>

              <InputWrap dir="rtl">
                <section>
                  <label> وصف</label>
                </section>
                <input type="text" placeholder=" وصف" />
              </InputWrap>
            </InputPageWrapper>
          </BoxContent>
          <MiddleContent style={{ marginTop: "20px" }}>
            <OrangeButton>SUBMIT</OrangeButton>
          </MiddleContent>
        </CreateWrapper>
      </Dialog>
    </div>
  );
}
