import React from "react";
import Dialog from "@mui/material/Dialog";
import useMediaQuery from "@mui/material/useMediaQuery";
import { useTheme } from "@mui/material/styles";
import { OrangeButton } from "../../../style/Gobalstyle";
import styled from "styled-components";
import InputField from "../../../validation/InputField";
import { Field, Form, Formik } from "formik";
import * as yup from "yup";
import "../../../style/CustomClasses.css";

export default function ChangePassword({ open, handleClose }) {
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down("sm"));

  return (
    <div>
      <Dialog
        fullScreen={fullScreen}
        open={open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <Cancel>
          <i className="icon-CloseCross" onClick={() => handleClose()} />
        </Cancel>
        <Formik
          initialValues={{
            email: "",
            password: "",
          }}
          //   validationSchema={schema}
          //   onSubmit={handleSubmit}
        >
          <Form>
            <ChangePasswordWrapper>
              <BoxContent>
                <header>Change Password</header>
                <div>
                  <label>Current Password</label>
                  <Field
                    type="password"
                    name="currentPassword"
                    placeholder="Current Password"
                    component={InputField}
                  />
                </div>

                <div>
                  <label>New Password</label>
                  <Field
                    type="password"
                    name="newPassword"
                    placeholder="New Password"
                    component={InputField}
                  />
                </div>

                <div>
                  <label>Confirm Password</label>
                  <Field
                    type="password"
                    name="confirmPassword"
                    placeholder="Confirm Password"
                    component={InputField}
                  />
                </div>
                <OrangeButton style={{ padding: "13px 30px" }}>
                  SUBMIT
                </OrangeButton>
              </BoxContent>
            </ChangePasswordWrapper>
          </Form>
        </Formik>
      </Dialog>
    </div>
  );
}

const Cancel = styled.span`
  width: 48px;
  height: 48px;
  background: #000;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  position: absolute;
  right: -18px;
  top: -18px;
  cursor: pointer;
`;

const ChangePasswordWrapper = styled.div`
  width: 413px;
  padding: 30px 20px;
`;

export const BoxContent = styled.div`
  width: 100%;
  padding: 0 15px;
  text-align: center;

  header {
    font-family: "Jost", sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 28px;
    line-height: 40px;
    color: #242424;
    padding: 20px 0;
  }
  label {
    text-align: start;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 15px;
    color: #000000;
    padding: 10px 0;
  }
  div {
    display: flex;
    flex-direction: column;
  }
  input {
    height: 48px;
    background: #fcfdfe;
    border: 1px solid #f0f1f7;
    border-radius: 8px;
    margin: 0 1px 20px 1px;
    padding: 0 16px;

    ::placeholder {
      font-family: "Jost", sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 18px;
      color: #989898;
    }
  }
`;
