import React from "react";
import Dialog from "@mui/material/Dialog";
import useMediaQuery from "@mui/material/useMediaQuery";
import { useTheme } from "@mui/material/styles";
import { Cancel } from "../MenuStyle";
import styled from "styled-components";
import { MenusectionImage1 } from "../../../Utils/Images";

export default function ViewSectionItem({ open, handleClose }) {
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down("lg"));

  return (
    <div>
      <Dialog
        fullScreen={fullScreen}
        open={open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <Cancel onClick={handleClose}>
          <i className="icon-CloseCross" />
        </Cancel>

        <SectionItemWrapper>
          <img src={MenusectionImage1} />

          <section>
            <div className="flex-space-between">
              <header>Fast Food</header>
              <span>
                <i className="icon-Edit" />
                <label>Edit</label>
              </span>
            </div>

            <DetailBox>
              <div>
                <label>Menus : </label>
                <span>Desserts</span>
              </div>
              <div>
                <label>Sections : </label>
                <span>Cake</span>
              </div>
              <div>
                <label>Allergies : </label>
                <span>Mushrooms, cabbage</span>
              </div>

              <div>
                <label>Sort Order ID : </label>
                <span>125694</span>
              </div>

              <div>
                <label>Recommended : </label>
                <span>Fries, Milkshake</span>
              </div>
            </DetailBox>

            <LabelSpanSection>
              <p>Price : </p>
              <Box>
                <div>
                  <label>Name : </label>
                  <span>Medium</span>
                  <VerticalLine />
                  <label>Price : </label>
                  <span>QAR 50</span>
                  <VerticalLine />

                  <label>Calories : </label>
                  <span>257 kcal</span>
                </div>

                <div>
                  <label>Name : </label>
                  <span>Medium</span>
                  <VerticalLine />
                  <label>Price : </label>
                  <span>QAR 50</span>
                  <VerticalLine />

                  <label>Calories : </label>
                  <span>257 kcal</span>
                </div>
              </Box>
            </LabelSpanSection>

            <LabelSpanSection>
              <p>Description :</p>
              <h1>
                Lorem Ipsum is simply dummy text of the printing and typesetting
                industry. Lorem Ipsum has been the industry's standard du mmy
                text ever since the 1500s
              </h1>
            </LabelSpanSection>

            <LabelSpanSection style={{ whiteSpace: "nowrap" }}>
              <p>Option Sets :</p>
              <Box>
                <div>
                  <label>Modifier group : </label>
                  <span>Extras</span>
                  <VerticalLine />
                  <label>Max : </label>
                  <span>0</span>
                  <VerticalLine />
                  <label>Min : </label>
                  <span>0</span>

                  <VerticalLine />

                  <label>Required : </label>
                  <span>No</span>
                </div>
              </Box>
            </LabelSpanSection>
          </section>
        </SectionItemWrapper>
      </Dialog>
    </div>
  );
}

const LabelSpanSection = styled.div`
  display: flex;
  margin: 10px 0;

  p {
    font-family: "Jost";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.08em;
    color: #000000;
    margin: 0 5px;
    white-space: nowrap;
  }
`;

const VerticalLine = styled.span`

width: 0px;
height: 20px;
border: 1px solid rgb(0 0 0 / 15%);
margin 0 10px;

`;

const Box = styled.div`
  background: #fcfdfe;
  border: 0.659039px solid #f0f1f7;
  border-radius: 5.27231px;
  margin: 0 20px;
  padding: 10px;
  width: 100%;

  div {
    font-family: "Jost";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 124.5%;
    letter-spacing: 0.08em;
    label {
      color: #000000 !important;
    }
    span {
      color: #141414;
    }
  }
`;

const DetailBox = styled.div`
  width: 100%;
  height: 102px;
  background: #fcfdfe;
  border: 0.659039px solid #f0f1f7;
  border-radius: 5.27231px;
  padding: 9px 14px;
  margin: 10px 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);

  div {
    font-family: "Jost";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.08em;
  }
  label {
    color: #000000 !important;
  }
  span {
    color: #646464;
  }
`;

const SectionItemWrapper = styled.div`
  width: 100%;
  height: 100%;
  background: #ffffff;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.25);
  border-radius: 4px;

  img {
    width: 100%;
    height: 260px;
    border-radius: 4px 4px 0 0;
  }

  section {
    padding: 20px;

    header {
      font-family: "Jost";
      font-style: normal;
      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
      color: #202020;
    }

    label {
      font-family: "Jost";
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      color: #f55a2c;
    }
    i {
      font-size: 14px;
      margin: 0 4px;
    }

    h1 {
      font-family: "Jost";
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 18px;

      letter-spacing: 0.08em;

      color: #141414;
    }
  }
`;
