import React from "react";
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import {
  CardWrapper,
  OrderAvailableCard,
  OrderOccupiedCard,
} from "../OrderStyle";

export default function Takeaway() {
  const direction = useSelector((state) => state.languageDirection.direction);
  const navigate = useNavigate();

  return (
    <div>
      <CardWrapper>
        <OrderOccupiedCard
          dir={direction}
          onClick={() => navigate("/aglut/orders/OrderDetails/:123")}
        >
          <section>
            <i className="icon-Dining" />
          </section>
          <div>
            <label>Status : </label>
            <span>Occupied</span>
          </div>
          <div>
            <label>QR Code Name : </label>
            <span>Table 01</span>
          </div>
          <div>
            <label>QR Code Group : </label>
            <span>Pool Tables</span>
          </div>
          <div>
            <label>Order Status : </label>
            <span>Collect Payment</span>
          </div>
        </OrderOccupiedCard>

        <OrderAvailableCard dir={direction}>
          <section>
            <i className="icon-Dining" />
          </section>
          <div>
            <label>Status : </label>
            <span>Occupied</span>
          </div>
          <div>
            <label>QR Code Name : </label>
            <span>Table 01</span>
          </div>
          <div>
            <label>QR Code Group : </label>
            <span>Pool Tables</span>
          </div>
          <div>
            <label>Order Status : </label>
            <span>Collect Payment</span>
          </div>
        </OrderAvailableCard>

        <OrderAvailableCard dir={direction}>
          <section>
            <i className="icon-Dining" />
          </section>
          <div>
            <label>Status : </label>
            <span>Occupied</span>
          </div>
          <div>
            <label>QR Code Name : </label>
            <span>Table 01</span>
          </div>
          <div>
            <label>QR Code Group : </label>
            <span>Pool Tables</span>
          </div>
          <div>
            <label>Order Status : </label>
            <span>Collect Payment</span>
          </div>
        </OrderAvailableCard>
      </CardWrapper>
    </div>
  );
}
