import React, { useState } from "react";
import { useSelector } from "react-redux";
import styled from "styled-components";
import ToggleSwitch from "../../../components/ToggleSwitch";
import { OrangeButton, SubHeader, WhiteButton } from "../../../style/Gobalstyle";
import { MenusectionImage3 } from "../../../Utils/Images";
import { CardWrapper, MenuCard } from "../MenuStyle";
import CreateSection from "./CreateSection";
import EditSection from "./EditSection";

export default function Section() {
  const direction = useSelector((state) => state.languageDirection.direction);
  const [isChecked, setIsChecked] = useState(false);
  const [openCreate, setOpenCreate] = useState(false);
  const [openEdit, setOpenEdit] = useState(false);

  const createSection = () => {
    setOpenCreate(true);
  };

  const editSection = () => {
    setOpenEdit(true);
  };

  return (
    <div>
      {openCreate && (
        <CreateSection
          open={openCreate}
          handleClose={() => setOpenCreate(false)}
        />
      )}

      {openEdit && (
        <EditSection open={openEdit} handleClose={() => setOpenEdit(false)} />
      )}
      <SubHeader>
        <p>Dessert Sections</p>
        <OrangeButton onClick={createSection}>CREATE</OrangeButton>
      </SubHeader>

      <SelectSection>
        <i className="icon-arrow" />
        <option>Main Course</option>
        <option>Main Course</option>
        <option>Main Course</option>
      </SelectSection>

      <CardWrapper>
        <MenuCard dir={direction}>
          <div className="upperSection">
            <img src={MenusectionImage3} alt="" />
          </div>
          <div className="hoverSection">
            <span>
              <i className="icon-Info" />
            </span>
            <OrangeButton onClick={editSection}>EDIT</OrangeButton>
            <WhiteButton>DELETE</WhiteButton>
          </div>
          <section>
            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <header>Main Menu</header>
              <ToggleSwitch
                label="MenuSection"
                isChecked={isChecked}
                onChange={(status) => setIsChecked(status)}
              />
            </div>
            <p>
              Lorem Ipsum is simply dummy text of the printing and typesetting
              industry.
            </p>
          </section>
        </MenuCard>
      </CardWrapper>
    </div>
  );
}

const SelectSection = styled.select`
  width: 100%;
  height: 55px;
  background: #fcfdfe;
  border: 1px solid #f0f1f7;
  border-radius: 8px;
  margin: 20px 0 0 0;

  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  padding: 0 22px;
  color: rgba(0, 0, 0, 0.6);
`;
