export const CardWrapper = styled.div`
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  margin: 20px 0 10px 0;
`;

export const TabButton = styled.div`
  width: 100%;
  height: 100%;
  display: flex;

  div {
    background: #ffffff;
    border: 1px solid #f55a2c;
    box-shadow: 0px 2px 16px rgba(61, 107, 192, 0.25);
    border-radius: 10px;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    padding: 10px 20px;
    margin: 5px 10px;
    cursor: pointer;
    color: #f55a2c;
    &.active {
      color: #fff;
      background: #f55a2c;
    }
  }
`;

export const DeleteButton = styled.div`
  width: 40px;
  height: 40px;
  background: #ffffff;
  border: 1px solid #f55a2c;
  box-shadow: 0px 2px 16px rgba(0, 0, 0, 0.25);
  border-radius: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: 0;
  top: 0;
  margin: 20px;

  i {
    font-size: 18px;
    color: #f55a2c;
  }
`;

export const TotalSection = styled.div`
  display: flex;
  justify-content: space-between;
  margin: 10px 0;

  label {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    /* identical to box height */

    color: #000000;
    margin: 0 10px;
    opacity: 0.6;
  }

  span {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 17px;
    line-height: 21px;
    /* identical to box height */

    letter-spacing: 0.05em;

    color: #f55a2c;
  }
`;

export const ExtraSection = styled.div`
  width: 100%;
  height: 150px;
  background: #ffffff;
  border: 1px solid #e1e1e1;
  border-radius: 10px;
  padding: 10px;
  display: flex;
  justify-content: space-between;

  .right {
    div {
      margin: 10px 0;
    }
  }

  .left {
    div {
      margin: 5px 0;
    }
  }

  label {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    margin: 0 10px;
    color: #000000;
  }

  button {
    width: 65px;
    height: 26px;
    left: 877px;
    top: 324px;

    background: #ffffff;
    border: 1px solid #e7e7e7;
    border-radius: 6px;
  }
`;

