import React from "react";
import { useNavigate } from "react-router-dom";
import {
  BoxContainer,
  HorizontalLines,
  OrangeButton,
  SubHeader,
} from "../../style/Gobalstyle";
import { Currentplan, PlanDetails } from "./InvoicesStyle";
import InvoicesTable from "./InvoicesTable";

export default function Invoices() {
  const navigate = useNavigate()

  const UserHeader = [
    {
      id: "Sr.No.",
      key: "SrNo",
    },
    {
      id: "Track ID",
      key: "Track_ID",
    },
    {
      id: "Plan Name",
      key: "Plan_Name",
    },
    {
      id: "Type",
      key: "Type",
    },
    {
      id: "Status",
      key: "Status",
    },
    {
      id: "From Date",
      key: "From_Date",
    },
    {
      id: "To Date",
      key: "To_Date",
    },

    {
      id: "Price",
      key: "Price",
    },

    {
      id: "Past Due Days",
      key: "PastDueDays",
    },
    {
      id: "Action",
      key: "action",
    },
  ];

  const TableAction = {
    apply: true,
    view: true,
  };

  const tableContents = [
    {
      Track_ID: "#5893366",
      Plan_Name: "ABC",
      Type: "Monthly",
      Status: "Pending",
      From_Date: "21 April 2021",
      To_Date: "21 April 2022",
      Price: "QAR 349",
      PastDueDays: "6 Days",
    },


    {
      Track_ID: "#5893366",
      Plan_Name: "ABC",
      Type: "Monthly",
      Status: "Pending",
      From_Date: "21 April 2021",
      To_Date: "21 April 2022",
      Price: "QAR 349",
      PastDueDays: "6 Days",
    },



    {
      Track_ID: "#5893366",
      Plan_Name: "ABC",
      Type: "Monthly",
      Status: "Paid",
      From_Date: "21 April 2021",
      To_Date: "21 April 2022",
      Price: "QAR 349",
      PastDueDays: "6 Days",
    },



    {
      Track_ID: "#5893366",
      Plan_Name: "ABC",
      Type: "Monthly",
      Status: "Paid",
      From_Date: "21 April 2021",
      To_Date: "21 April 2022",
      Price: "QAR 349",
      PastDueDays: "6 Days",
    },


  
  ];

  return (
    <div>
      <SubHeader>
        <p>Invoice $ Payments</p>
        <OrangeButton onClick={()=>navigate("/aglut/payment/plan")} >PLANS</OrangeButton>
      </SubHeader>
      <BoxContainer>
        <Currentplan>
          <header>Current Plan</header>
          <div>
            <label>Next Due Date:</label>
            <span>25 April 2023</span>
          </div>
        </Currentplan>
        <HorizontalLines />
        <PlanDetails>
          <section>
            <div>
              <label>Plan Name:</label>
              <span>ABC</span>
            </div>

            <div>
              <label>Type: </label>
              <span>Monthly</span>
            </div>

            <div>
              <label>Duration: </label>
              <span>23 April 2022 To 24 April 2023</span>
            </div>

            <div>
              <label>Price: </label>
              <span>QAR 369</span>
            </div>
          </section>

          <section>
            <div>
              <label></label>
              <span></span>
            </div>

            <div>
              <label></label>
              <span></span>
            </div>

            <div>
              <label></label>
              <span></span>
            </div>

            <div>
              <label>Status: </label>
              <span style={{ color: "red" }}>Pending</span>
            </div>
          </section>
        </PlanDetails>
      </BoxContainer>

      <BoxContainer>
        <InvoicesTable
          header={UserHeader}
          tableData={tableContents}
          action={TableAction}
        />
      </BoxContainer>
    </div>
  );
}
