import React from "react";
import { BoxContainer } from "../../../style/Gobalstyle";
import OrderTable from "../OrderTable";

export default function Completed() {
  const UserHeader = [
    {
      id: "Order ID",
      key: "OrderID",
    },

    {
      id: "Date",
      key: "Date",
    },
    {
      id: "OrderType",
      key: "OrderType",
    },
    {
      id: "TableNumber",
      key: "TableNumber",
    },

    {
      id: "ModeofPayment",
      key: "ModeofPayment",
    },
    {
      id: "Price",
      key: "Price",
    },
    {
      id: "Action",
      key: "Action",
    },
  ];

  const tableContents = [
    {
      OrderID: "01475",
      Date: "18-07-2022",
      OrderType: "Dine in",
      TableNumber: "1",
      ModeofPayment: "Cash",
      Price: "QAR 3256",
    },
  ];

  const TableAction = {
    apply: true,
    showDetail: true,
    // showDetailPath:"/aglut/orders/completed/details/"
  };

  return (
    <div>
      <BoxContainer>
        <OrderTable
          header={UserHeader}
          tableData={tableContents}
          action={TableAction}
          subheader={[]}
          subcontents={[]}
        />
      </BoxContainer>
    </div>
  );
}
