import React from "react";
import Dialog from "@mui/material/Dialog";
import useMediaQuery from "@mui/material/useMediaQuery";
import { useTheme } from "@mui/material/styles";
import { useSelector } from "react-redux";
import { Cancel } from "../../../style/Gobalstyle";
import { CreateQRWrapper } from "../QRStyle";
import styled from "styled-components";

export default function ViewQR({ open, handleClose }) {
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down("sm"));
  const direction = useSelector((state) => state.languageDirection.direction);

  return (
    <div>
      <Dialog
        fullScreen={fullScreen}
        open={open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <Cancel onClick={handleClose}>
          <i className="icon-CloseCross" />
        </Cancel>

        <ViewQRWrapper>
          <img src="http://api.qrserver.com/v1/create-qr-code/?data=${knjfvfd}" />
          <h1>Table 01</h1>
          <p>Outside Tables</p>
        </ViewQRWrapper>
      </Dialog>
    </div>
  );
}

const ViewQRWrapper = styled.div`
  width: 380px;
  background: #ffffff;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding:50px 20px;

  img {
    margin: 30px 0;
  }

  h1 {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 28px;
    line-height: 34px;
    letter-spacing: 0.1em;
    color: #000000;
    margin: 10px 0;

  }

  p {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 0.05em;
    color: #000000;
  }
`;
