import React, { useState } from "react";
import { useSelector } from "react-redux";
import styled from "styled-components";
import ToggleSwitch from "../../../components/ToggleSwitch";
import { OrangeButton, SubHeader, WhiteButton } from "../../../style/Gobalstyle";
import { MenusectionImage1, MenusectionImage2 } from "../../../Utils/Images";
import { CardWrapper, MenuCard } from "../MenuStyle";
import CreateMenu from "./CreateMenu";
import EditMenu from "./EditMenu";

export default function Menu() {
  const direction = useSelector((state) => state.languageDirection.direction);
  const [isChecked, setIsChecked] = useState(false);
  const [openCreate, setOpenCreate] = useState(false);
  const [openEdit, setOpenEdit] = useState(false);
  const createMenu = () => {
    setOpenCreate(true);
  };

  const editMenu = () => {
    setOpenEdit(true);
  };

  return (
    <div>
      {openCreate && (
        <CreateMenu
          open={openCreate}
          handleClose={() => setOpenCreate(false)}
        />
      )}

      {openEdit && (
        <EditMenu open={openEdit} handleClose={() => setOpenEdit(false)} />
      )}
      <SubHeader>
        <p>Menus</p>
        <OrangeButton onClick={createMenu}>CREATE</OrangeButton>
      </SubHeader>
      <CardWrapper>
        <MenuCard dir={direction}>
          <div className="upperSection">
            <img src={MenusectionImage1} alt="" />
          </div>
          <div className="hoverSection">
            <span>
              <i className="icon-Info" />
            </span>
            <OrangeButton onClick={editMenu}>EDIT</OrangeButton>
            <WhiteButton>DELETE</WhiteButton>
          </div>
          <section>
            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <header>Main Menu</header>
              <ToggleSwitch
                label="MainMenu"
                isChecked={isChecked}
                onChange={(status) => setIsChecked(status)}
              />
            </div>
            <p>
              Lorem Ipsum is simply dummy text of the printing and typesetting
              industry.
            </p>
          </section>
        </MenuCard>

        <MenuCard dir={direction}>
          <div className="upperSection">
            <img src={MenusectionImage2} alt="" />
          </div>
          <div className="hoverSection">
            <span>
              <i className="icon-Info" />
            </span>
            <OrangeButton onClick={editMenu}>EDIT</OrangeButton>
            <WhiteButton>DELETE</WhiteButton>
          </div>
          <section>
            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <header>Main Menu</header>
              <ToggleSwitch
                label="MainMenu"
                isChecked={isChecked}
                onChange={(status) => setIsChecked(status)}
              />
            </div>
            <p>
              Lorem Ipsum is simply dummy text of the printing and typesetting
              industry.
            </p>
          </section>
        </MenuCard>

        <MenuCard dir={direction}>
          <div className="upperSection">
            <img src={MenusectionImage1} alt="" />
          </div>
          <div className="hoverSection">
            <span>
              <i className="icon-Info" />
            </span>
            <OrangeButton onClick={editMenu}>EDIT</OrangeButton>
            <WhiteButton>DELETE</WhiteButton>
          </div>
          <section>
            <div style={{ display: "flex", justifyContent: "space-between" }}>
              <header>Main Menu</header>
              <ToggleSwitch
                label="MainMenu"
                isChecked={isChecked}
                onChange={(status) => setIsChecked(status)}
              />
            </div>
            <p>
              Lorem Ipsum is simply dummy text of the printing and typesetting
              industry.
            </p>
          </section>
        </MenuCard>
      </CardWrapper>
    </div>
  );
}
