import React, { useEffect, useState } from "react";
import Dialog from "@mui/material/Dialog";
import useMediaQuery from "@mui/material/useMediaQuery";
import { useTheme } from "@mui/material/styles";
import { useSelector } from "react-redux";
import { Cancel, OrangeButton } from "../../../style/Gobalstyle";
import { CreateQRWrapper, InputWrap } from "../QRStyle";

export default function EditQR({ open, handleClose }) {
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down("sm"));
  const direction = useSelector((state) => state.languageDirection.direction);


  const [qrtable, setQrtable] = useState(null);
  const [tableNumber, setTableNumber] = useState(null);
  const [qrCode, setQrCode] = useState(null);

  const generateQR = () => {
    setQrtable(tableNumber);
  };

  useEffect(() => {
    if (qrtable) {
      setQrCode(
        `http://api.qrserver.com/v1/create-qr-code/?data=${qrtable}!&size=${190}x${190}&bgcolor=${"#ffffff"}`
      );
    }
  }, [qrtable]);

  return (
    <div>
      <Dialog
        fullScreen={fullScreen}
        open={open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <Cancel onClick={handleClose}>
          <i className="icon-CloseCross" />
        </Cancel>


        <CreateQRWrapper>
          <header>Edit QR Code</header>

          <InputWrap dir={direction}>
            <section>
              <label>QR Code Name / اسم رمز الاستجابة السريعة </label>
            </section>
            <input
              dir="ltr"
              type="text"
              placeholder="QR Code Name"
              onChange={(e) => {
                setTableNumber(e.target.value);
              }}
            />
            <input
              dir="rtl"
              type="text"
              placeholder="سم رمز الاستجابة السريعة "
            />
          </InputWrap>

          <InputWrap dir={direction}>
            <section>
              <label>Menu Group</label>
            </section>
            <select>
              <option selected="true" disabled hidden></option>
              <option>Extras chesses</option>
              <option>Extras topping</option>
            </select>
          </InputWrap>
          <p onClick={generateQR}>Re-Generate QR Code</p>
          <img src={qrCode} alt="" />
          <div style={{ textAlign: "center" }}>
            <OrangeButton>SUBMIT</OrangeButton>
          </div>
        </CreateQRWrapper>



      </Dialog>
    </div>
  );
}
