import React from "react";
import { Dialog } from "@mui/material";
import useMediaQuery from "@mui/material/useMediaQuery";
import { useTheme } from "@mui/material/styles";
import { ButtonWrapper, Cancel, OrangeButton } from "../../style/Gobalstyle";
import styled from "styled-components";
import { useNavigate } from "react-router-dom";
import { AddOrderWrapper, BoxContent, InputWrap, OptionSetWrapAddOrder } from "./OrderStyle";

export default function AddOrder({ open, handleClose }) {
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down("sm"));
  // const direction = useSelector((state) => state.languageDirection.direction);
  const navigate = useNavigate();

  return (
    <div>
      <Dialog
        fullScreen={fullScreen}
        open={open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <Cancel onClick={handleClose}>
          <i className="icon-CloseCross" />
        </Cancel>
        <AddOrderWrapper>
          <BoxContent>
            <header>Add Order</header>

            <InputWrap>
              <section>
                <label>Customer name</label>
              </section>
              <input dir="ltr" type="text" placeholder="Customer name" />
            </InputWrap>

            <OptionSetWrapAddOrder>
              <div>
                <p>QR Code Group</p>
                <select>
                  <option selected="true" disabled hidden></option>
                  <option>Extras chesses</option>
                  <option>Extras topping</option>
                </select>
              </div>
            </OptionSetWrapAddOrder>

            <OptionSetWrapAddOrder>
              <div>
                <p>QR Code Name</p>
                <select>
                  <option selected="true" disabled hidden></option>
                  <option>Extras chesses</option>
                  <option>Extras topping</option>
                </select>
              </div>
            </OptionSetWrapAddOrder>

            <OptionSetWrapAddOrder>
              <div>
                <p>Order Type</p>
                <select>
                  <option selected="true" disabled hidden></option>
                  <option>Extras chesses</option>
                  <option>Extras topping</option>
                </select>
              </div>
            </OptionSetWrapAddOrder>

            <OptionSetWrapAddOrder>
              <div>
                <p>Phone Number</p>
                <select>
                  <option selected="true" disabled hidden></option>
                  <option>Extras chesses</option>
                  <option>Extras topping</option>
                </select>
              </div>
            </OptionSetWrapAddOrder>
          </BoxContent>
          <ButtonWrapper>
            <OrangeButton
              onClick={() => navigate("/aglut/orders/addorder/details")}
            >
              SUBMIT
            </OrangeButton>
          </ButtonWrapper>
        </AddOrderWrapper>
      </Dialog>
    </div>
  );
}
