import React, { useEffect, useState } from "react";
import Dialog from "@mui/material/Dialog";
import useMediaQuery from "@mui/material/useMediaQuery";
import { useTheme } from "@mui/material/styles";
import { Cancel, OrangeButton } from "../../../style/Gobalstyle";
import styled from "styled-components";
import { useSelector } from "react-redux";
// import { CreateQRWrapper, InputWrap } from "../QRStyle";


export default function OptionModuleCreate({ open, handleClose }) {
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down("lg"));
  const direction = useSelector((state) => state.languageDirection.direction);

  const [qrtable, setQrtable] = useState(null);
  const [tableNumber, setTableNumber] = useState(null);
  const [qrCode, setQrCode] = useState(null);

  const generateQR = () => {
    setQrtable(tableNumber);
  };

  useEffect(() => {
    if (qrtable) {
      setQrCode(
        `http://api.qrserver.com/v1/create-qr-code/?data=${qrtable}!&size=${190}x${190}&bgcolor=${"#ffffff"}`
      );
    }
  }, [qrtable]);

  return (
    <div>
      <Dialog
        fullScreen={fullScreen}
        open={open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <Cancel onClick={handleClose}>
          <i className="icon-CloseCross" />
        </Cancel>

        <CreateQRWrapper>
          <header>Add a Modifier Group</header>

          <InputWrap dir={direction}>
            <section>
              <label>Name / اسم</label>
            </section>
            <input
              dir="ltr"
              type="text"
              placeholder="Name"
              onChange={(e) => {
                setTableNumber(e.target.value);
              }}
            />
            <input
              dir="rtl"
              type="text"
              placeholder="اسم"
            />
          </InputWrap>

          <InputWrap dir={direction}>
            <section>
              <label>Menu Group</label>
            </section>
            <select>
              <option selected="true" disabled hidden></option>
              <option>Extras chesses</option>
              <option>Extras topping</option>
            </select>
          </InputWrap>
          <p onClick={generateQR}>Generate QR Code</p>
          <img src={qrCode} alt="" />
          <div style={{ textAlign: "center" }}>
            <OrangeButton>SUBMIT</OrangeButton>
          </div>
        </CreateQRWrapper>
      </Dialog>
    </div>
  );
}


const CreateQRWrapper = styled.div`
  width: 600px;
  background: #ffffff;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
  padding: 30px 20px;

  img {
    margin: 20px 0;
  }

  p {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    letter-spacing: 0.05em;
    text-decoration-line: underline;
    color: #f55a2c;
  }

  header {
    text-align: center;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    color: #000000;
  }
`;



const InputWrap = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-top: 20px;

  section {
    display: flex;
  }

  label {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    color: #000000;
    opacity: 0.6;
  }

  select {
    width: 100%;
    height: 42px;
    background: #fcfdfe;
    border: 1px solid #f0f1f7;
    border-radius: 8px;
    padding: 0 10px;
    margin: 5px 0;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
  }
  select:focus {
    outline: none;
    border: 1px solid #e1e1e1;
    box-shadow: 0px 2px 8px #f55a2c;
  }

  input {
    width: 100%;
    height: 42px;
    background: #fcfdfe;
    border: 1px solid #f0f1f7;
    border-radius: 8px;
    padding: 0 10px;
    margin: 5px 0;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
  }
  input:focus {
    outline: none;
    border: 1px solid #e1e1e1;
    box-shadow: 0px 2px 8px #f55a2c;
  }

  span {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 10px;
    line-height: 12px;
    text-align: end;
    padding: 5px 0;
    cursor: pointer;
    color: #f55a2c;
  }
`;