import React, { useState } from "react";
import styled from "styled-components";
import { BoxContainer, SubHeader } from "../../style/Gobalstyle";

export default function InvoicePlan() {
  const [selectedPlan, setSelectedPlan] = useState(null);
  const SelectPlan = (e) => {
    setSelectedPlan(e.target.value);
  };
  return (
    <div>
      <SubHeader>
        <p>Plans</p>
      </SubHeader>
      <BoxContainer>
        <InvoicePlanWrapper>
          <header>Pick a plan to start after your trial</header>
          <PlanCardWrap>
            <PlanCard selplan={selectedPlan}>
              <input
                type="radio"
                name="plan"
                value="299"
                id="1"
                onClick={SelectPlan}
              />
              <div>
                <header>
                  QAR 299/<span>Month + GST</span>
                </header>
                <p>QAR 3600 billed annually</p>
              </div>
            </PlanCard>

            <PlanCard selplan={selectedPlan}>
              <input
                type="radio"
                name="plan"
                value="399"
                id="2"
                onClick={SelectPlan}
              />
              <div>
                <header>
                  QAR 399/<span>Month + GST</span>
                </header>
                <p>QAR 3600 billed annually</p>
              </div>
            </PlanCard>
          </PlanCardWrap>
        </InvoicePlanWrapper>
      </BoxContainer>
    </div>
  );
}

const PlanCard = styled.div`
  background: #ffffff;

  // border: ${(selectPlan) =>selectPlan === "299" ? "2px solid #f55a2c" : "2px solid #000"}
  // border: ${({ selplan }) => console.log(selplan, "selectPlan---------")}
  
  border: 2px solid #F55A2C;
  border-radius: 10px;
  display: flex;
  align-items: center;
  padding: 20px;

  input {
    width: 18px;
    height: 18px;
    background: #000;
    border: 1px solid #f55a2c;
    border-radius: 50%;
    margin: 0 10px;
  }

  header {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: #3d3d3d;
  }

  span {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    color: #3d3d3d;
  }

  p {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    padding: 5px;
    color: #5a6172;
  }
`;

const PlanCardWrap = styled.div`
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin: 20px 0;
`;

const InvoicePlanWrapper = styled.div`
  header {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: #313131;
  }
`;
