import React from "react";
import Dialog from "@mui/material/Dialog";
import useMediaQuery from "@mui/material/useMediaQuery";
import { useTheme } from "@mui/material/styles";
import { ButtonWrapper, Cancel, OrangeButton } from "../../style/Gobalstyle";
import styled from "styled-components";
import {
  BoxContent,
  DeleteButton,
  DetailBox,
  FieldGrid3,
  OptionSetWrap,
} from "./OrderStyle";

export default function EditItem({ open, handleClose }) {
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down("sm"));

  return (
    <div>
      <Dialog
        fullScreen={fullScreen}
        open={open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <Cancel onClick={handleClose}>
          <i className="icon-CloseCross" />
        </Cancel>

        <DialogWrapper>
          <BoxContent>
            <header>Edit Item</header>
          </BoxContent>

          <DetailBox>
            <section style={{ width: "100%" }}>
              <FieldGrid3>
                <InputWrap>
                  <label>Name</label>
                  <input dir="ltr" type="text" placeholder="Name" />
                </InputWrap>

                <InputWrap>
                  <label>Phone Number</label>
                  <input dir="ltr" type="text" placeholder="Phone Number" />
                </InputWrap>

                <OptionSetWrap>
                  <div>
                    <p>Status</p>
                    <select>
                      <option selected="true" disabled hidden></option>
                      <option>Extras chesses</option>
                      <option>Extras topping</option>
                    </select>
                  </div>
                </OptionSetWrap>
              </FieldGrid3>
            </section>

            <section style={{ width: "100%" }}>
              <FieldGrid3>
                <OptionSetWrap>
                  <div>
                    <p>Section</p>
                    <select>
                      <option selected="true" disabled hidden></option>
                      <option>Extras chesses</option>
                      <option>Extras topping</option>
                    </select>
                  </div>
                </OptionSetWrap>

                <OptionSetWrap>
                  <div>
                    <p>Section Item</p>
                    <select>
                      <option selected="true" disabled hidden></option>
                      <option>Extras chesses</option>
                      <option>Extras topping</option>
                    </select>
                  </div>
                </OptionSetWrap>

                <InputWrap>
                  <label>Quantity</label>
                  <input type="number" placeholder="Quantity" />
                </InputWrap>
              </FieldGrid3>
              <InputWrap>
                <label>Special Note</label>

                <input dir="ltr" type="text" placeholder="Special Note" />
              </InputWrap>
            </section>
          </DetailBox>

          <ButtonWrapper>
            <OrangeButton>SUBMIT</OrangeButton>
          </ButtonWrapper>
        </DialogWrapper>
      </Dialog>
    </div>
  );
}

export const InputWrap = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;

  section {
    display: flex;
  }

  label {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    color: #000000;
    opacity: 0.6;
  }

  input {
    width: 100%;
    height: 42px;
    background: #fcfdfe;
    border: 1px solid #f0f1f7;
    border-radius: 8px;
    padding: 0 10px;
    margin: 5px 0;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
  }
  input:focus {
    outline: none;
    border: 1px solid #e1e1e1;
    box-shadow: 0px 2px 8px #f55a2c;
  }
`;

const DialogWrapper = styled.div`
  width: 600px;
  padding: 10px 20px;
  background: #ffffff;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.25);
  border-radius: 16px;
`;
