import React, { useState } from "react";
import { useSelector } from "react-redux";
import styled from "styled-components";
import ToggleSwitch from "../../../components/ToggleSwitch";
import {
  BlackButton,
  BoxContainer,
  ButtonWrapper,
  OrangeButton,
  SubHeader,
  WhiteButton,
} from "../../../style/Gobalstyle";
import { ColorPick } from "../../../Utils/Images";

export default function QRMenuBranding() {
  const direction = useSelector((state) => state.languageDirection.direction);

  const [webpageColor, setWebpageColor] = useState({
    background: "",
    buttonText: "",
    buttonBackground: "",
  });

  const [homepageColor, setHomepageColor] = useState({
    background: "",
    text: "",
    priceText: "",
    buttonText: "",
    buttonBackground: "",
    heading: "",
  });

  return (
    <div>
      <SubHeader>
        <p>QR Menu Branding</p>
      </SubHeader>

      <BoxContainer>
        <BrandingContainer>
          <WelcomePage>
            <header>Welcome Page</header>
            <section>
              <InputWrap dir={direction}>
                <label>Display Image for Web page background</label>
              </InputWrap>
              <div style={{ margin: "10px 0" }}>
                <ToggleSwitch label="Whatsapp" />
              </div>
            </section>

            <section>
              <Upload>
                <input type="file" />
                <i className="icon-Upload" />
                <span>Upload Images Here</span>
              </Upload>
              <WhiteButton>CHANGE</WhiteButton>
              <OrangeButton>REMOVE</OrangeButton>
            </section>

            <section>
              <InputWrap dir={direction}>
                <label>Webpage background color</label>
                <div>
                  <span>{webpageColor.background}</span>
                  <input
                    type="color"
                    onChange={(e) =>
                      setWebpageColor({ background: e.target.value })
                    }
                  />
                  <img src={ColorPick} alt="color" />
                </div>
              </InputWrap>
            </section>

            <section>
              <InputWrap dir={direction}>
                <label>Welcome button text color</label>
                <div>
                  <span>{webpageColor.buttonText}</span>
                  <input
                    type="color"
                    onChange={(e) =>
                      setWebpageColor({ buttonText: e.target.value })
                    }
                  />
                  <img src={ColorPick} alt="color" />
                </div>
              </InputWrap>
            </section>

            <section>
              <InputWrap dir={direction}>
                <label>Welcome button background color</label>
                <div>
                  <span>{webpageColor.buttonBackground}</span>
                  <input
                    type="color"
                    onChange={(e) =>
                      setWebpageColor({ buttonBackground: e.target.value })
                    }
                  />
                  <img src={ColorPick} alt="color" />
                </div>
              </InputWrap>
            </section>


          </WelcomePage>

          <HomePage>
            <header>Welcome Page</header>

            <section>
              <InputWrap dir={direction}>
                <label>Webpage background color</label>
                <div>
                  <span>{homepageColor.background}</span>
                  <input
                    type="color"
                    onChange={(e) =>
                      setHomepageColor({ background: e.target.value })
                    }
                  />
                  <img src={ColorPick} alt="color" />
                </div>
              </InputWrap>
            </section>

            <section>
              <InputWrap dir={direction}>
                <label>Text color</label>
                <div>
                  <span>{homepageColor.text}</span>
                  <input
                    type="color"
                    onChange={(e) => setHomepageColor({ text: e.target.value })}
                  />
                  <img src={ColorPick} alt="color" />
                </div>
              </InputWrap>
            </section>

            <section>
              <InputWrap dir={direction}>
                <label>Price Text Color</label>
                <div>
                  <span>{homepageColor.priceText}</span>
                  <input
                    type="color"
                    onChange={(e) =>
                      setHomepageColor({ priceText: e.target.value })
                    }
                  />
                  <img src={ColorPick} alt="color" />
                </div>
              </InputWrap>
            </section>

            <section>
              <InputWrap dir={direction}>
                <label>Home button text color</label>
                <div>
                  <span>{homepageColor.buttonText}</span>
                  <input
                    type="color"
                    onChange={(e) =>
                      setHomepageColor({ buttonText: e.target.value })
                    }
                  />
                  <img src={ColorPick} alt="color" />
                </div>
              </InputWrap>
            </section>

            <section>
              <InputWrap dir={direction}>
                <label>Home button background color</label>
                <div>
                  <span>{homepageColor.buttonBackground}</span>
                  <input
                    type="color"
                    onChange={(e) =>
                      setHomepageColor({ buttonBackground: e.target.value })
                    }
                  />
                  <img src={ColorPick} alt="color" />
                </div>
              </InputWrap>
            </section>

            <section>
              <InputWrap dir={direction}>
                <label>Heading text color</label>
                <div>
                  <span>{homepageColor.heading}</span>
                  <input
                    type="color"
                    onChange={(e) =>
                      setHomepageColor({ heading: e.target.value })
                    }
                  />
                  <img src={ColorPick} alt="color" />
                </div>
              </InputWrap>
            </section>
          </HomePage>
        </BrandingContainer>
<ButtonWrapper>

        <BlackButton>GENERATE</BlackButton>
</ButtonWrapper>
      </BoxContainer>
    </div>
  );
}

export const Upload = styled.div`
  width: 193px;
  height: 100px;
  background: #ffffff;
  border: 1px solid #e6e6f1;
  position: relative;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 20px 0 10px 0;

  input {
    width: 193px;
    height: 100px;
    opacity: -1;
    position: absolute;
  }

  i {
    font-size: 25px;
    padding: 10px 0;
  }

  span {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 10px;
    line-height: 12px;
    text-align: center;

    color: #f55a2c;
  }
`;

const HomePage = styled.div`
  width: 100%;

  header {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: #000000;
  }
`;

const WelcomePage = styled.div`
  width: 100%;

  header {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: #000000;
  }
`;

const BrandingContainer = styled.div`
  // padding:20px;
  display: flex;

  section {
    margin: 10px 0;
  }
`;

export const InputWrap = styled.div`
  width: 90%;
  display: flex;
  flex-direction: column;

  label {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    color: #000000;
    opacity: 0.6;
  }

  div {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    height: 42px;
    background: #fcfdfe;
    border: 1px solid #f0f1f7;
    border-radius: 8px;
    padding: 0 20px;
    margin: 10px 0;
  }

  input {
    position: absolute;
    top: 8px;
    opacity: 0;
    z-index: 9;
    right: 20px;
    width: 20px;
    height: 20px;
    cursor: pointer;
  }
  img {
    position: absolute;
    top: 9px;
    right: 20px;
    width: 20px;
    height: 20px;
  }
`;
