import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { Outlet } from "react-router-dom";
import styled from "styled-components";
import Header from "../../components/Header";
import SideNav from "../../components/SideNav";
import { DashboardWrapper, Page } from "./Dashboardstyle";

export default function Dashboard() {
  const Direction = useSelector((state) => state.languageDirection.direction);

  const [width, setWidth] = React.useState(window.innerWidth);

  React.useEffect(() => {
    const handleWindowResize = () => setWidth(window.innerWidth);
    window.addEventListener("resize", handleWindowResize);
    return () => window.removeEventListener("resize", handleWindowResize);
  }, []);

  const [open, setOpen] = useState(false);
  const con = () => {
    setOpen(!open);
  };

  return (
    <DashboardWrapper>
      {width > 991 && <SideNav Direction={Direction} />}

      {width < 991 && open && <SideNav Direction={Direction} />}

      {width < 991 && <ControlNav className="icon-Cross" onClick={con} />}
      <Header />
      <Page dir={Direction}>
        <Outlet />
      </Page>
    </DashboardWrapper>
  );
}

const ControlNav = styled.i`
  position: absolute;
  font-size: 28px;
  z-index: 9;
  left: 230px;
  top: 10px;
  width: 50px;
  height: 50px;
  backgrpound: yellow;
`;
