import React, { useEffect, useState } from "react";
import Dialog from "@mui/material/Dialog";
import useMediaQuery from "@mui/material/useMediaQuery";
import { useTheme } from "@mui/material/styles";
import { useSelector } from "react-redux";
import { Cancel, OrangeButton } from "../../../style/Gobalstyle";
import { CreateQRWrapper, InputWrap } from "../QRStyle";
import styled from "styled-components";

export default function EditMenuGroup({ open, handleClose }) {
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down("sm"));
  const direction = useSelector((state) => state.languageDirection.direction);

  return (
    <div>
      <Dialog
        fullScreen={fullScreen}
        open={open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <Cancel onClick={handleClose}>
          <i className="icon-CloseCross" />
        </Cancel>

        <CreateQRWrapper>
          <header>Edit QR Code</header>

          <InputWrap dir={direction}>
            <section>
              <label>Group Type</label>
            </section>
          </InputWrap>
          <SelectGroup>
            <input type="radio" value="dineIN" name="GroupType" />
            <label>Dine In</label>
            <input type="radio" value="takeaway" name="GroupType" />
            <label>Takeaway</label>
          </SelectGroup>

          <InputWrap dir={direction}>
            <section>
              <label>QR Code Name / اسم رمز الاستجابة السريعة </label>
            </section>
            <input dir="ltr" type="text" placeholder="QR Code Name" />
            <input
              dir="rtl"
              type="text"
              placeholder="سم رمز الاستجابة السريعة "
            />
          </InputWrap>

          <div style={{ textAlign: "center" }}>
            <OrangeButton>SUBMIT</OrangeButton>
          </div>
        </CreateQRWrapper>
      </Dialog>
    </div>
  );
}

const SelectGroup = styled.div`
  display: flex;
  align-items: center;
  label {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    /* identical to box height */

    letter-spacing: 0.05em;
    margin: 10px 5px;
    color: #000000;
  }

  input {
    width: 20px;
    height: 20px;
  }
`;
