import React from "react";
import styled from "styled-components";
import {
  BoxContainer,
  ButtonWrapper,
  HorizontalLines,
  OrangeButton,
  SubHeader,
} from "../../style/Gobalstyle";
import { Currentplan } from "./InvoicesStyle";

export default function InvoicesView() {
  return (
    <div>
      <SubHeader>
        <p>Invoice</p>
        <OrangeButton>PRINT INVOICE</OrangeButton>
      </SubHeader>
      <BoxContainer>
        <Currentplan>
          <header>Invoice Details</header>
        </Currentplan>
        <HorizontalLines />

        <InvoicesDetails>
          <section className="firstSection">
            <div>
              <label>Subscription Start Date:</label>
              <span>24 December 2022</span>
            </div>

            <div>
              <label>Subscription End Date:</label>
              <span>24 December 2022</span>
            </div>

            <div>
              <label>Past Due Days:</label>
              <span>21 Days</span>
            </div>
          </section>
          <section>
            <div>
              <label>Status:</label>
              <span>Pending</span>
            </div>

            <div>
              <label>Subscription Amount:</label>
              <span>QAR 550</span>
            </div>
          </section>
        </InvoicesDetails>
        <HorizontalLines />

        <ButtonWrapper>
          <OrangeButton style={{ padding: "10px 40px" }}>PAY</OrangeButton>
        </ButtonWrapper>
      </BoxContainer>
    </div>
  );
}



const InvoicesDetails = styled.div`
  display: flex;
  section {
    width: 100%;
  }

  .firstSection {
    border-right: 1px solid rgba(0, 0, 0, 0.2);
    margin: 0 10px;
  }

  div {
    margin: 5px 0;
  }
  label {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.08em;
    color: #000000;
  }

  span {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 0.08em;
    color: #646464;
    margin: 0 10px;
  }
`;
