import styled from "styled-components";

// Header Component styling
// Header START
export const List = styled.div`
  width: 100% !important;
  justify-content: center !important;
  align-items: center;
  cursor: pointer;
  border: none;
  position: absolute !important;
  z-index: 4;
  top: 100%;
  left: 0;
  padding: 0;
  background: #ffffff;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: rgba(0, 0, 0, 0.6);
  box-shadow: 0px 2px 8px rgba(61, 107, 192, 0.28);
  li {
    height: 35px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    &:hover {
      background: rgba(0, 0, 0, 0.1);
    }
  }
`;

export const LanguageSelect = styled.div`
  width: 94px;
  height: 48px;
  border: none;
  border-radius: 10px;
  position: relative;
  background: #ffffff;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: rgba(0, 0, 0, 0.6);
  box-shadow: 0px 2px 8px rgba(61, 107, 192, 0.28);
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all ease-in 0.3s;
  div {
    //   width:45px;
    padding: 0 5px;
    display: flex;
    justify-content: space-between;
    text-transform: capitalize;
  }
  .select-icon {
    transition: all ease-in 0s;
    margin: 0 3px;
    font-size: 8px;
    transform: ${({ open }) =>
      !open ? "rotate(180deg) translateY(5px)" : "translateY(5px)"};
  }
`;

export const InfoSection = styled.div`
  width: 100px;
  display: flex;
  margin: 0 10px;

  img {
    width: 40px;
    height: 40px;
    background: #000;
    border-radius: 50%;
    background-size: cover;
  }

  p {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: #000000;
    padding: 0 5px;
  }
`;

export const InnerBox = styled.div`
  width: 325px;
  height: 60px;
  margin: 0 40px;
  display: flex;
  align-items: center;
  position: absolute;
  left: ${({ dir }) => dir === "rtl" && 0};
  right: ${({ dir }) => dir === "ltr" && 0};

  select {
    width: 94px;
    height: 42px;
    margin: 0 10px;
    background: #ffffff;
    box-shadow: 0px 2px 8px rgba(61, 107, 192, 0.28);
    border-radius: 10px;
  }
`;

export const HeaderWrapper = styled.div`
  width: 100%;
  height: 80px;
  background: #ffffff;
  border: 1px solid #d7d7d7;
  display: flex;
  justify-content: end;
  align-items: center;
  position: fixed;
  top: 0;
  z-index: 1;
`;


export const SubHeader = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  p {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    color: #000000;
  }
`;



export const IconsWrap = styled.div`
  margin: 0 10px;
  font-size: 25px;
  line-height: 17px;
  color: inherit;
`;

// Header END

// Button

//Button styling START HERE

export const OrangeButton = styled.button`
  background: #f55a2c;
  box-shadow: 0px 2px 16px rgba(61, 107, 192, 0.25);
  border-radius: 10px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: #ffffff;
  padding: 10px 20px;
  margin: 5px 10px;
  cursor: pointer;
`;

export const WhiteButton = styled.button`
  background: #ffffff;
  border: 1px solid #f55a2c;
  box-shadow: 0px 2px 16px rgba(61, 107, 192, 0.25);
  border-radius: 10px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  color: #f55a2c;
  padding: 10px 20px;
  margin: 5px 10px;
  cursor: pointer;
`;

export const BlackButton = styled.button`
  background: #000000;
  box-shadow: 0px 2px 16px rgba(61, 107, 192, 0.25);
  border-radius: 10px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #ffffff;
  padding: 10px 20px;
  margin: 5px 10px;
  cursor: pointer;
`;

//Button styling End HERE




// Filters Style


export const FilterButton = styled.div`
  background: #ffffff;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.28);
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 13px 10px;
  margin: 0 6px 0 6px;
  cursor: pointer;

  i {
    font-size: 14px;
    margin: 0 6px 0 6px;
  }

  p {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    color: #a1a1a1;
    margin: 0 6px 0 6px;
  }
`;

export const FilterSection = styled.div`
  display: flex;
  justify-content: end;
`;

export const BoxContainer = styled.div`
  width: 100%;
  height: 100%;
  padding: 20px 30px;
  background: #ffffff;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.28);
  border-radius: 10px;
  margin: 20px 0;
`;
















export const Cancel = styled.span`
  width: 48px;
  height: 48px;
  background: #000;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  cursor: pointer;
  position: absolute;
  right: -18px;
  top: -18px;
`;




export const HorizontalLines = styled.div`
  width: 100%;
  height: 1px;
  background: rgba(0, 0, 0, 0.2);
  margin: 10px 0;
`;


export const ButtonWrapper = styled.div`
width: 100%;
display: flex;
justify-content: center;
`;