import React, { useState } from "react";
import { useSelector } from "react-redux";
import ChangePassword from "../../app/Auth/ChangePassword/ChangePassword";
import Toggle from "../../components/ToggleSwitch";
import { BlackButton, OrangeButton, SubHeader, WhiteButton } from "../../style/Gobalstyle";
import { ProfilePicture } from "../../Utils/Images";
import IntlMessage from "../../Utils/IntlMessage";

import {
  BoxContainer,
  InnerBoxContainer,
  InnerLeftWrapper,
  InputPageLeftWrapper,
  InputPageRightWrapper,
  InputPageWrapper,
  InputWrap,
  LeftSection,
  ProfileWrapper,
  RightSection,
  WeekInput,
  WeekSelect,
} from "./Profilestyle";

export default function Profile() {
  const direction = useSelector((state) => state.languageDirection.direction);
  const [openChangePassword, setOpenChangePassword] = useState(false);

  return (
    <ProfileWrapper>
      {openChangePassword && (
        <ChangePassword
          open={openChangePassword}
          handleClose={() => setOpenChangePassword(false)}
        />
      )}
      <SubHeader>
        <p>
          <IntlMessage id="sidebar.profile" />
        </p>
        <OrangeButton onClick={() => setOpenChangePassword(true)}>
          <IntlMessage id="button.CHANGEPASSWORD" />
        </OrangeButton>
      </SubHeader>
      <BoxContainer>
        <InnerBoxContainer>
          <LeftSection>
            <section>
              <img src={ProfilePicture} alt="Profile" />
            </section>
            <div>
              <WhiteButton>CHANGE PHOTO</WhiteButton>
              <OrangeButton>REMOVE PHOTO</OrangeButton>
            </div>
          </LeftSection>
          <RightSection>
            <section>
              <p>Disable Orders</p>
              <Toggle label="Disable Orders" />
            </section>
            {/* <section>
              <p>Enable Payments</p>
              <Toggle label="Enable Payments" />
            </section> */}
          </RightSection>
        </InnerBoxContainer>
      </BoxContainer>

      <BoxContainer>
        <InputPageWrapper>
          <InputWrap dir={direction}>
            <section>
              <i className="icon-Shop" />
              <label>Restaurant Name</label>
            </section>
            <input dir="ltr" type="text" placeholder="Restaurant Name" />
            <input dir="rtl" type="text" placeholder="Restaurant Name Arabic" />
          </InputWrap>

          <InputWrap dir={direction}>
            <section>
              <i className="icon-Dollor" />
              <label>Currency</label>
            </section>
            <input type="text" placeholder="Currency" />
          </InputWrap>
          <InputWrap dir={direction}>
            <section>
              <i className="icon-Message" />
              <label>Mail Address</label>
            </section>
            <input type="text" placeholder="Currency" />
          </InputWrap>
          <InputPageWrapper>
            <InputWrap dir={direction}>
              <section>
                <i className="icon-Vat" />
                <label>VAT %</label>
              </section>
              <input type="text" placeholder="Currency" />
            </InputWrap>

            <InputWrap dir={direction}>
              <section>
                <i className="icon-Dollor" />
                <label>Service Charge %</label>
              </section>
              <input type="text" placeholder="Currency" />
            </InputWrap>
          </InputPageWrapper>

          <InputPageLeftWrapper>
            <InnerLeftWrapper>
              <InputWrap dir={direction}>
                <section>
                  <i className="icon-Address" />
                  <label>Address</label>
                </section>
                <input type="text" placeholder="Address" />
              </InputWrap>

              <InputWrap dir={direction}>
                <section>
                  <i className="icon-TimeZone" />
                  <label>Time Zone</label>
                </section>
                <input type="text" placeholder="Time Zone" />
              </InputWrap>

              <InputWrap dir={direction}>
                <section>
                  <i className="icon-PhoneNumber" />
                  <label>Phone Number</label>
                </section>
                <input type="text" placeholder="Phone Number" />
                <input type="text" placeholder="Phone Number" />
                <span>+Add new</span>
              </InputWrap>

              <section>
                <InputWrap dir={direction}>
                  <section>
                    <i className="icon-WhatsApp" />
                    <label>Whatsapp Notifications</label>
                  </section>
                </InputWrap>
                <div style={{ margin: "5px 0 0 28px" }}>
                  <Toggle label="Whatsapp" />
                </div>
              </section>

              <InputWrap dir={direction}>
                <section>
                  <i className="icon-Bio" />
                  <label>Restaurant Bio</label>
                </section>
                <input dir="ltr" type="text" placeholder="Restaurant Bio" />
                <input dir="ltr" type="text" placeholder="Restaurant Bio" />
              </InputWrap>
            </InnerLeftWrapper>
          </InputPageLeftWrapper>
          <InputPageRightWrapper>
            <InputWrap dir={direction}>
              <section>
                <i className="icon-OpeningTime" />
                <label>Opening Times</label>
              </section>
            </InputWrap>
            <WeekSelect>
              {/* Sunday started here */}

              <WeekInput dir={direction}>
                <label>Sunday</label>
                <input
                  className="textInput"
                  type="text"
                  placeholder="Open time"
                />
                <input
                  className="textInput"
                  type="text"
                  placeholder="Close time"
                />
                <input className="checkInput" type="checkbox" />
                <span>Close</span>
              </WeekInput>

              {/* Sunday End here  */}

              {/* Monday start here */}

              <WeekInput dir={direction}>
                <label>Monday</label>
                <input
                  className="textInput"
                  type="text"
                  placeholder="Open time"
                />
                <input
                  className="textInput"
                  type="text"
                  placeholder="Close time"
                />
                <input className="checkInput" type="checkbox" />
                <span>Close</span>
              </WeekInput>
              {/* Monday end here */}

              {/* Tuesday start here */}
              <WeekInput dir={direction}>
                <label>Tuesday</label>
                <input
                  className="textInput"
                  type="text"
                  placeholder="Open time"
                />
                <input
                  className="textInput"
                  type="text"
                  placeholder="Close time"
                />
                <input className="checkInput" type="checkbox" />
                <span>Close</span>
              </WeekInput>
              {/* Tuesday end here */}

              <WeekInput dir={direction}>
                <label>Wednesday</label>
                <input
                  className="textInput"
                  type="text"
                  placeholder="Open time"
                />
                <input
                  className="textInput"
                  type="text"
                  placeholder="Close time"
                />
                <input className="checkInput" type="checkbox" />
                <span>Close</span>
              </WeekInput>

              <WeekInput dir={direction}>
                <label>Thursday</label>
                <input
                  className="textInput"
                  type="text"
                  placeholder="Open time"
                />
                <input
                  className="textInput"
                  type="text"
                  placeholder="Close time"
                />
                <input className="checkInput" type="checkbox" />
                <span>Close</span>
              </WeekInput>

              <WeekInput dir={direction}>
                <label>Friday</label>
                <input
                  className="textInput"
                  type="text"
                  placeholder="Open time"
                />
                <input
                  className="textInput"
                  type="text"
                  placeholder="Close time"
                />
                <input className="checkInput" type="checkbox" />
                <span>Close</span>
              </WeekInput>

              <WeekInput dir={direction}>
                <label>Saturday</label>
                <input
                  className="textInput"
                  type="text"
                  placeholder="Open time"
                />
                <input
                  className="textInput"
                  type="text"
                  placeholder="Close time"
                />
                <input className="checkInput" type="checkbox" />
                <span>Close</span>
              </WeekInput>
            </WeekSelect>
          </InputPageRightWrapper>
        </InputPageWrapper>
        <div style={{ textAlign: "center", margin: "20px 0" }}>
          <BlackButton>UPDATE</BlackButton>
        </div>
      </BoxContainer>
    </ProfileWrapper>
  );
}
