import React, { useState } from "react";
import {
  BoxContainer,
  OrangeButton,
  SubHeader,
} from "../../../style/Gobalstyle";
import QRTable from "../QRTable";
import CreateMenuGroup from "./CreateMenuGroup";

export default function ORMenuGroup() {
  const [openCreateMenuGroup, setOpenCreateMenuGroup] = useState(false);

  const UserHeader = [
    {
      id: "Group Type",
      key: "Group_Type",
    },
    {
      id: "Group Name",
      key: "Group_Name",
    },
    {
      id: "Action",
      key: "Action",
    },
    {
      id: "",
      key: "Toggle",
    },
  ];

  const TableAction = {
    apply: true,
    view: true,
    edit: true,
    delete: true,
    toggle: true,
    actionOfView: "OpenViewQRMenuGroup",
    actionOfEdit:"OpenEditQRMenuGroup"
    //please
  };

  const tableContents = [
    {
      Group_Type: "Dine In",
      Group_Name: "Outside Tables",
      active: true,
    },
    {
      Group_Type: "Takeaway",
      Group_Name: "Pool Tables",
      active: true,
    },
  ];

  const OpenCreateQR = () => {
    setOpenCreateMenuGroup(true);
  };

  return (
    <div>
      {openCreateMenuGroup && (
        <CreateMenuGroup
          open={openCreateMenuGroup}
          handleClose={() => setOpenCreateMenuGroup(false)}
        />
      )}
      <SubHeader>
        <p>QR Menu Groups</p>
        <OrangeButton onClick={OpenCreateQR}>CREATE MENU GROUP</OrangeButton>
      </SubHeader>

      <BoxContainer>
        <QRTable
          header={UserHeader}
          tableData={tableContents}
          action={TableAction}
        />
      </BoxContainer>
    </div>
  );
}
