import React, { useState } from "react";
import {
  BoxContainer,
  OrangeButton,
  SubHeader,
} from "../../../style/Gobalstyle";
import QRTable from "../QRTable";
import CreateQR from "./CreateQR";

export default function QRCode() {
  const [openCreateQR, setOpenCreateQR] = useState(false);
  const UserHeader = [
    {
      id: "QR Code Name",
      key: "QR_CodeName",
    },
    {
      id: "QR Source",
      key: "QR_Source",
    },
    {
      id: "Group Name",
      key: "Group_Name",
    },
    {
      id: "QR Code",
      key: "QR_Code",
    },
    {
      id: "Action",
      key: "Action",
    },
    {
      id: "",
      key: "Toggle",
    },
  ];

  const TableAction = {
    apply: true,
    view: true,
    edit: true,
    delete: true,
    toggle: true,
    actionOfView: "OpenViewQR",
    actionOfEdit: "openEditQR",
  };

  const tableContents = [
    {
      QR_CodeName: "Table 01",
      QR_Source: "Dine In",
      Group_Name: "Outside Tables",
      active: true,
    },

    {
      QR_CodeName: "Table 02",
      QR_Source: "Dine In",
      Group_Name: "Pool Tables",
      active: true,
    },
  ];

  const OpenCreateQR = () => {
    setOpenCreateQR(true);
  };

  return (
    <div>
      {openCreateQR && (
        <CreateQR
          open={openCreateQR}
          handleClose={() => setOpenCreateQR(false)}
        />
      )}
      <SubHeader>
        <p>Order Details</p>
        <OrangeButton onClick={OpenCreateQR}>CREATE QR CODE</OrangeButton>
      </SubHeader>

      <BoxContainer>
        <QRTable
          header={UserHeader}
          tableData={tableContents}
          action={TableAction}
        />
      </BoxContainer>
    </div>
  );
}
