import styled from "styled-components";



export const RightSection = styled.div`
display: flex;
flex-direction: column;
justify-content: center;
section {
  display: flex;
  margin: 10px 0;
}

p {
  width: 125px;
  text-align: end;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #000000;
  white-space: nowrap;
  margin: 0 10px;
}
`;



export const LeftSection = styled.div`
display: flex;
width: 100%;
height: 100%;
align-items: center;

section {
  width: 100px;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 20px 0 0;
  border-radius: 50%;
  background: #ffffff;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.25);
  
}
div {
  display: flex;
  flex-direction: column;
}
`;

export const InnerBoxContainer = styled.div`
width: 100%;
hieght: 100%;
display: flex;
justify-content: space-between;
`;

export const BoxContainer = styled.div`
width: 100%;
height: 100%;
padding: 20px 30px;
background: #ffffff;
box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.28);
border-radius: 10px;
margin: 20px 0;
`;



export const ProfileWrapper = styled.div`
width: 100%;
height: 100%;
position: relative;
`;





export const WeekInput = styled.div`
  width: 100%;
  height: 100%;
  padding: 0px 30px;
  display: flex;
  align-items: center;
  label {
    width: 100%;
    text-align: end;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
  }
  .textInput {
    width: 100%;
    height: 42px;
    background: #fcfdfe;
    border: 1px solid #f0f1f7;
    border-radius: 8px;
    padding: 0 10px;
    margin: 8px 15px 0 15px;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
  }

  .checkInput {
    width: 18px;
    height: 18px;
    background: red;
  }

  span {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
    margin: 0 9px;
  }
`;

export const WeekSelect = styled.div``;

export const InnerLeftWrapper = styled.div`
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 20px;
`;

export const InputPageRightWrapper = styled.div`
  width: 100%;
  height: 100%;
`;

export const InputPageLeftWrapper = styled.div`
  width: 100%;
  height: 100%;
`;

export const InputWrap = styled.div`
  width: 90%;
  display: flex;
  flex-direction: column;

  section {
    display: flex;
  }

  i {
    font-size: 18px;
  }

  label {
    margin: 0 10px;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    color: #000000;
    opacity: 0.6;
  }

  input {
    width: 100%;
    height: 42px;
    background: #fcfdfe;
    border: 1px solid #f0f1f7;
    border-radius: 8px;
    padding: 0 20px;
    margin: 8px 25px 0 25px;
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
  }
  input:focus {
    outline: none;
    border: 1px solid #e1e1e1;
    box-shadow: 0px 2px 8px #f55a2c;
  }

  span {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 10px;
    line-height: 12px;
    text-align: end;
    padding: 5px 0;
    cursor: pointer;
    color: #f55a2c;
  }
`;

export const InputPageWrapper = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
`;
