import React, { useState } from "react";
import { useSelector } from "react-redux";
import { Navigate, useNavigate } from "react-router-dom";
import styled from "styled-components";
import EditItem from "./EditItem";
import { Table, TableWrapper, TBody, THead } from "./OrderStyle";

export default function OrderTable({
  header,
  tableData,
  action,
  subheader,
  subcontents,
}) {
  const direction = useSelector((state) => state.languageDirection.direction);

  return (
    <TableWrapper>
      <Table>
        <thead>
          <TableHeader data={header} dir={direction} />
        </thead>
        <div className="forMargin" />
        {tableData.map((item, index) => (
          <TableBody
            header={header}
            body={item}
            action={action}
            index={index}
            id={item.id}
            dir={direction}
            subheader={subheader}
            subcontents={subcontents}
          />
        ))}
      </Table>
    </TableWrapper>
  );
}

const TableHeader = ({ data, dir }) => {
  return (
    <THead dir={dir}>
      {data.map((item, index) => (
        <th key={index}>{item.id}</th>
      ))}
    </THead>
  );
};

const TableBody = ({ body, header, action, dir, subheader, subcontents }) => {
  const OpenViewSection = () => {};
  return (
    <tbody className="tbody">
      <TBody dir={dir}>
        {header.map((el) => (
          <td style={{ padding: "0 5px" }}>{body[el.key]}</td>
        ))}
      </TBody>
      <div className="forMargin" />

      {subheader && subcontents && (
        <tr>
          <td colSpan={6}>
            <SubTable>
              <thead>
                <SubHead dir={dir}>
                  {subheader.map((item, index) => (
                    <th key={index}>{item.id}</th>
                  ))}
                </SubHead>
              </thead>
              <div className="forMargin" />

              {subcontents.map((item, index) => (
                <>
                  <TBody dir={dir}>
                    {subheader.map((el) => (
                      <td style={{ padding: "0 5px" }}>
                        {el.id === "Action" && action.apply ? (
                          <TableAction action={action} data={body} />
                        ) : (
                          item[el.key]
                        )}
                      </td>
                    ))}
                  </TBody>
                  <div className="forMargin" />
                </>
              ))}
            </SubTable>
          </td>
        </tr>
      )}
    </tbody>
  );
};

const TableAction = ({ action, data }) => {
  const navigate = useNavigate();
  const [openEditItem, setOpenEditItem] = useState(false);

  return (
    <ActionDiv>
      {openEditItem && (
        <EditItem
          open={openEditItem}
          handleClose={() => setOpenEditItem(false)}
        />
      )}
      {action.view && <i className="icon-View" />}
      {action.showDetail && (
        <ShowDetail
          onClick={() => navigate("/aglut/orders/completed/details/:123")}
        >
          Show Details
        </ShowDetail>
      )}
      {action.edit && (
        <i className="icon-Edit" onClick={() => setOpenEditItem(true)} />
      )}
      {action.delete && <i className="icon-Delete" />}
    </ActionDiv>
  );
};

const ShowDetail = styled.div`
  background: #e1e1e1;
  border: 1px solid #b9b9b9;
  border-radius: 10px;
  padding: 8px 16px;
  cursor: pointer;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 10px;
  line-height: 12px;

  color: #515151;
`;

export const ActionDiv = styled.div`
  display: flex;
  justify-content: center;
  i {
    font-size: 16px;
    margin: 0 3px;
    cursor: pointer;
  }
`;

const SubHead = styled.tr`
  width: 100%;
  border-radius: 6px;

  th {
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;

    color: #353535;

    &:first-child {
      border-radius: ${({ dir }) => dir === "ltr" && "6px 0 0 6px"};
      border-radius: ${({ dir }) => dir === "rtl" && "0 6px 6px 0"};
    }
    &:last-child {
      border-radius: ${({ dir }) => dir === "ltr" && "0 6px 6px 0"};
      border-radius: ${({ dir }) => dir === "rtl" && "6px 0 0 6px"};
    }
  }
`;

const SubTable = styled.table`
  width: 100%;
  border-spacing: 0;

  thead {
    height: 42px;
    background: #e2e2e2;
  }

  .forMargin {
    margin: 4px 0;
  }
`;

const ShowHide = styled.div`
  width: 55px;
  background: #ffffff;
  border: 1px solid #f55a2c;
  box-shadow: 0px 2px 16px rgba(61, 107, 192, 0.25);
  border-radius: 5px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 10px;
  line-height: 12px;
  padding: 6px 14px;
  color: #f55a2c;
  cursor: pointer;
`;
